/** file -> base64*/
const fileToBlob = (file: any, callback: any) => {
  const newFile = new FileReader();
  newFile.readAsDataURL(file);
  newFile.onload = (e: any) => {
    callback(e.currentTarget.result);
  };
};

const downloadExportFile = (
  blob: any,
  fileName: any = "img",
  fileType: any = "png"
) => {
  const downloadElement = document.createElement("a");
  let href = blob;
  if (typeof blob === "string") {
    downloadElement.target = "_blank";
  } else {
    href = window.URL.createObjectURL(blob); // 创建下载的链接
  }
  downloadElement.href = href;
  downloadElement.download = fileName + "." + fileType; // 下载后文件名
  document.body.appendChild(downloadElement);
  downloadElement.click(); // 触发点击下载
  document.body.removeChild(downloadElement); // 下载完成移除元素
  if (typeof blob !== "string") {
    window.URL.revokeObjectURL(href); // 释放掉blob对象
  }
};

const base64ToBlob = (dataurl: any) => {
  const arr = dataurl.split(",");
  const mime = arr[0].match(/:(.*?);/)[1];
  console.log(arr);
  const bstr: any = atob(arr[1]);
  let n = bstr.length;
  const u8arr = new Uint8Array(n);
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }
  return new Blob([u8arr], { type: mime });
};

// * desc: base64转文件并下载
// * @param base64 {String} : base64数据
// * @param fileType {String} : 要导出的文件类型png,pdf,doc,mp3等
// * @param fileName {String} : 文件名

const downloadFile = (
  base64: any,
  fileName: any = "img",
  fileType: any = "png"
) => {
  const typeHeader = "data:application/" + fileType + ";base64,"; // 定义base64 头部文件类型
  const converedBase64 = typeHeader + base64; // 拼接最终的base64
  const blob = base64ToBlob(converedBase64); // 转成blob对象
  downloadExportFile(blob, fileName, fileType); // 下载文件
};

/** blob图片下载 */
const blobDownLoad = (file: any, fileType: string, filename: string) => {
  downloadExportFile(file, filename, fileType); // 下载文件
};

/** 生成随机字符串 */
const randomString = (len = 12) => {
  const chars = "ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678";
  const maxPos = chars.length;
  let pwd = "";
  for (let i = 0; i < len; i++) {
    pwd += chars.charAt(Math.floor(Math.random() * maxPos));
  }
  return pwd;
};

/** 图片压缩 */
const imageCompress = (
  base64: any,
  next: any,
  type: string,
  quality: number
) => {
  const img = new Image();
  img.src = base64;
  // eslint-disable-next-line @typescript-eslint/no-this-alias
  img.onload = function () {
    let canvasWidth: any;
    let canvasHeight: any;
    console.log(img.width, img.height);

    let maxWidth = 300;

    if (img.width >= img.height) {
      maxWidth = img.width > maxWidth ? maxWidth : img.width;
      canvasWidth = maxWidth;
      canvasHeight = (maxWidth / img.width) * img.height;
    } else {
      maxWidth = img.height > maxWidth ? maxWidth : img.height;
      canvasHeight = maxWidth;
      canvasWidth = (maxWidth / img.height) * img.width;
    }
    //生成canvas
    const canvas = document.createElement("canvas");
    const ctx: any = canvas.getContext("2d");
    //创建节点属性
    canvas.width = canvasWidth;
    canvas.height = canvasHeight;
    const anw: any = document.createAttribute("width");
    anw.nodeValue = canvasWidth;
    const anh: any = document.createAttribute("height");
    anh.nodeValue = canvasHeight;
    canvas.setAttributeNode(anw);
    canvas.setAttributeNode(anh);
    ctx.drawImage(this, 0, 0, canvasWidth, canvasHeight);
    //quality 图像质量,值越小，所绘制出的图像越模糊
    const base64String = canvas.toDataURL(type, quality);
    //通过base64获取二进制文件
    const blob: any = getBlobByBase64(base64String);
    console.log("blob", blob);
    //上传图片
    next({
      content: base64String,
      blob: blob,
    });
  };
};

const getBlobByBase64 = (dataBase64: string, filename = "file") => {
  const arr = dataBase64.split(",");
  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
  // @ts-ignore
  const mime = arr[0].match(/:(.*?);/)[1]; //设置file文件流的type名称
  const suffix = mime.split("/")[1]; //设置file文件流的name名称
  const bstr = window.atob(arr[1]);
  let n = bstr.length;
  const u8arr = new Uint8Array(n);
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }
  return new File([u8arr], `${filename}.${suffix}`, {
    type: mime,
  });
};

export { fileToBlob, downloadFile, randomString, imageCompress, blobDownLoad };
