/** 引入axios **/
import axios from "axios";
import findUrl from "./setUrl";

/**  创建axios实例 ***/
const httpService = axios.create({
  baseURL: process.env.BASE_API, // 需自定义
  /** 请求超时时间 **/
  timeout: 3000, // 需自定义
});

/** request拦截器 **/
httpService.interceptors.request.use(
  (config) => {
    /** 根据条件加入token-安全携带 **/
    // if (true) { // 需自定义
    /** 让每个请求携带token,   此处添加请求头 **/
    const token: any = sessionStorage.getItem("token");
    config.headers = {
      token,
    };
    // }
    return config;
  },
  (error) => {
    /** 请求错误处理 **/
    Promise.reject(error);
  }
);

/** 网络请求部分 **/

/**  get请求
 *  url:请求地址
 *  params:参数
 **/
export function get(url: any, params = {}) {
  return new Promise((resolve, reject) => {
    httpService({
      url: findUrl(url),
      method: "get",
      params: params,
    })
      .then((response) => {
        resolve(response);
      })
      .catch((error) => {
        reject(error);
      });
  });
}

/**  post请求
 *  url:请求地址
 *  params:参数
 **/
export function post(url: any, params = {}) {
  return new Promise((resolve, reject) => {
    httpService({
      url: findUrl(url),
      method: "post",
      data: params,
    })
      .then((response) => {
        resolve(response);
      })
      .catch((error) => {
        reject(error);
      });
  });
}

/**  文件上传
 *  url:请求地址
 *  params:参数
 **/
export function upload(
  url: any,
  params = {
    url: undefined,
  }
) {
  return new Promise((resolve, reject) => {
    httpService({
      url: findUrl(url), // 上传地址
      method: "post",
      data: params.url, // 上传的参数
    })
      .then((response) => {
        resolve(response);
      })
      .catch((error) => {
        reject(error);
      });
  });
}

export default {
  get,
  post,
  upload,
};
